.btn-page .btn {
  margin-right: 5px;
  margin-bottom: 5px;
}
.btn-page .btn-group-vertical .btn,
.btn-page .btn-group .btn {
  margin-right: 0;
  margin-bottom: 0;
}

/**  =====================
      Radio & Checked css start
==========================  **/
.checkbox {
  padding: 10px 0;
  min-height: auto;
  position: relative;
  margin-right: 5px;
}
.checkbox input[type=checkbox] {
  margin: 0;
  display: none;
  width: 22px;
}
.checkbox input[type=checkbox] + .cr {
  padding-left: 0;
}
.checkbox input[type=checkbox] + .cr:before {
  content: "";
  width: 22px;
  height: 22px;
