/* Modal  */
.modal .modal-header h4.modal-title {
  margin: 0px;
  color: #7044cd;
  font-weight: bold;
  width: 100%;
}
.modal .modal-header .close {
  cursor: pointer;
}

.modal.left .modal-dialog,
.modal.right .modal-dialog,
.modal.top .modal-dialog,
.modal.bottom .modal-dialog {
  -webkit-transform: translate3d(0%, 0, 0);
  -ms-transform: translate3d(0%, 0, 0);
  -o-transform: translate3d(0%, 0, 0);
  transform: translate3d(0%, 0, 0);
}

/* Bottom */
.modal.bottom .modal-dialog {
  position: fixed;
  margin: auto;
  width: 100%;
  max-width: 100%;
  height: auto;
}
.modal.bottom .modal-dialog .modal-content {
  border-radius: 0;
  border: none;
}
.modal.bottom .modal-dialog .modal-content .modal-header {
  padding: 5px 20px;
  border-bottom: none;
}
.modal.bottom .modal-dialog .modal-content .modal-header .modal-title {
  text-align: left;
  font: normal normal normal 18px/1.5 Helvetica Neue;
  letter-spacing: 0px;
  color: #474747;
}
.modal.bottom .modal-dialog .modal-content .modal-header .modal-title .count {
  text-align: left;
  font: normal normal bold 30px/40px Helvetica Neue;
  letter-spacing: 0px;
  color: #ffcc74;
  vertical-align: middle;
}
.modal.bottom .modal-dialog .modal-content .sk-chase {
  width: 40px;
  height: 40px;
  -webkit-animation: sk-chase 2.5s infinite linear both;
  animation: sk-chase 2.5s infinite linear both;
  margin: auto;
  z-index: 9999;
  position: absolute !important;
  top: 35% !important;
  left: 50% !important;
}

.modal.bottom .stage-header {
  padding: 0px !important;
}
.modal.bottom .stage-header .close {
  padding: 0;
  margin: 0px;
  width: 36px;
  height: 41px;
}

.modal.bottom.fade .modal-dialog {
  bottom: -100%;
  -webkit-transition: opacity 0.3s linear, bottom 0.3s ease-out;
  -moz-transition: opacity 0.3s linear, bottom 0.3s ease-out;
  -o-transition: opacity 0.3s linear, bottom 0.3s ease-out;
  transition: opacity 0.3s linear, bottom 0.3s ease-out;
}

.modal.bottom.fade.show .modal-dialog {
  bottom: 0;
}

/* Right Modal */
.modal.right .modal-dialog {
  position: fixed;
  margin: auto;
  height: 100%;
  -webkit-transform: translate3d(0%, 0, 0);
  -ms-transform: translate3d(0%, 0, 0);
  -o-transform: translate3d(0%, 0, 0);
  transform: translate3d(0%, 0, 0);
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: -6px -11px 6px #00000029;
}
.modal.right .modal-dialog .modal-content {
  height: 100vh;
  border-radius: 0;
  border: none;
}
.modal.right .modal-dialog .modal-content .modal-header {
  padding: 5px 20px !important;
  border-bottom: none;
}

.modal.right.fade .modal-dialog {
  right: -80%;
  -webkit-transition: opacity 0.3s linear, right 0.3s ease-out;
  -moz-transition: opacity 0.3s linear, right 0.3s ease-out;
  -o-transition: opacity 0.3s linear, right 0.3s ease-out;
  transition: opacity 0.3s linear, right 0.3s ease-out;
}

.modal.right.fade.show .modal-dialog {
  right: 0;
  width: calc(100% - 20%) !important;
  max-width: calc(100% - 20%) !important;
}

/* Top Modal */
.modal.top .modal-dialog {
  position: fixed;
  margin: auto;
  right: 0px;
  left: 0px;
  z-index: 999;
  /*  width: 100%;
  max-width: 100%; */
  height: auto;
}
.modal.top .modal-content {
  border-radius: 0;
  border: none;
  position: relative;
}
.modal.top .modal-content .modal-header {
  padding: 5px 20px;
}
.modal.top .modal-content .modal-header .modal-title {
  color: #7044cd;
  font: normal normal normal 20px/1.5 Helvetica Neue;
  letter-spacing: 0px;
}

.modal.top.fade .modal-dialog {
  top: -100%;
  -webkit-transition: opacity 0.3s linear, top 0.3s ease-out;
  -moz-transition: opacity 0.3s linear, top 0.3s ease-out;
  -o-transition: opacity 0.3s linear, top 0.3s ease-out;
  transition: opacity 0.3s linear, top 0.3s ease-out;
}

.modal.top.fade.show .modal-dialog {
  top: 0;
}

