/* Toggle Button 
  <div class="custom-toggle-btn">
    <span class="switcher switcher-1">
        <input type="checkbox" id="itemCheck"
            [attr.inter-label-true]="'YES'" [attr.inter-label-false]="'NO'" />
        <label for="itemCheck"></label>
    </span>
  </div>
*/
.custom-toggle-btn span.switcher {
  position: relative;
}
.custom-toggle-btn span.switcher input {
  cursor: pointer;
  appearance: none;
  position: relative;
  width: calc(100% - 0%);
  height: 25px;
  border-radius: 25px;
  background-color: #1e1e1e;
  outline: none;
  font-family: "Helvetica Neue", sans-serif;
  border: 1px solid lightgray;
}
.custom-toggle-btn span.switcher input:before, .custom-toggle-btn span.switcher input:after {
  z-index: 2;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: #ffffff;
}
.custom-toggle-btn span.switcher input:before {
  content: attr(inter-label-true);
  left: 15px;
}
.custom-toggle-btn span.switcher input:after {
  content: attr(inter-label-false);
  right: 15px;
}
.custom-toggle-btn span.switcher input[disabled] {
  cursor: not-allowed;
}
.custom-toggle-btn span.switcher label {
  z-index: 1;
  position: absolute;
  top: 10px;
  bottom: 10px;
  border-radius: 20px;
}
.custom-toggle-btn span.switcher.switcher-1 input {
  transition: 0.25s -0.1s;
}
.custom-toggle-btn span.switcher.switcher-1 input:checked {
  background: #ffffff;
  transition: background 0.5s -0.1s;
}
.custom-toggle-btn span.switcher.switcher-1 input:checked:before {
  transition: color 0.5s;
  background: #7044cd 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 6px #00000029;
  border-radius: 25px;
  left: 0px;
  width: calc(100% - 50%);
  height: 25px;
  opacity: 1;
  text-align: center;
  font: normal normal 400 14px/1.5 Helvetica Neue;
  letter-spacing: 0px;
  color: #ffffff;
  padding-top: 1px;
}
.custom-toggle-btn span.switcher.switcher-1 input:checked:after {
  transition: color 0.5s 0.2s;
  font: normal normal 400 14px/1.5 Helvetica Neue;
  letter-spacing: 0px;
  color: #c1c1c1;
  opacity: 1;
  margin-left: 15px;
  text-align: center;
}
.custom-toggle-btn span.switcher.switcher-1 input:checked + label {
  left: 100px;
  right: 10px;
  background: #ffffff;
  transition: left 0.4s 0.2s, right 0.5s, background 0.35s -0.1s;
}
.custom-toggle-btn span.switcher.switcher-1 input:not(:checked) {
  background-color: #ffffff;
}
.custom-toggle-btn span.switcher.switcher-1 input:not(:checked):before {
  transition: color 0.5s 0.2s;
  font: normal normal 400 14px/1.5 Helvetica Neue;
  letter-spacing: 0px;
  color: #c1c1c1;
  opacity: 1;
  text-align: center;
}
.custom-toggle-btn span.switcher.switcher-1 input:not(:checked):after {
  background: #ff8a73 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 6px #00000029;
  border-radius: 25px;
  left: calc(100% - 50%);
  width: calc(100% - 50%);
  height: 25px;
  opacity: 1;
  text-align: center;
  font: normal normal 400 14px/1.5 Helvetica Neue;
  letter-spacing: 0px;
  color: #ffffff;
  padding-top: 1px;
  transition: color 0.5s;
}
.custom-toggle-btn span.switcher.switcher-1 input:not(:checked) + label {
  left: 10px;
  right: 100px;
  background: #1e1e1e;
  transition: left 0.5s, right 0.4s 0.2s;
}

