/* Modal  */

.modal {
    .modal-header {
        h4.modal-title {
            margin: 0px;
            color: #7044cd;
            font-weight: bold;
            width: 100%;
        }

        .close {
            cursor: pointer;
        }
    }
}

.modal.left .modal-dialog,
.modal.right .modal-dialog,
.modal.top .modal-dialog,
.modal.bottom .modal-dialog {
    -webkit-transform: translate3d(0%, 0, 0);
    -ms-transform: translate3d(0%, 0, 0);
    -o-transform: translate3d(0%, 0, 0);
    transform: translate3d(0%, 0, 0);
}

/* Bottom */

.modal.bottom {
    .modal-dialog {
        position: fixed;
        margin: auto;
        width: 100%;
        max-width: 100%;
        height: auto;

        .modal-content {
            border-radius: 0;
            border: none;

            .modal-header {
                padding: 5px 20px;
                border-bottom: none;

                .modal-title {
                    text-align: left;
                    font: normal normal normal 18px/1.5 Helvetica Neue;
                    letter-spacing: 0px;
                    color: #474747;

                    .count {
                        text-align: left;
                        font: normal normal bold 30px/40px Helvetica Neue;
                        letter-spacing: 0px;
                        color: #ffcc74;
                        vertical-align: middle;
                    }
                }
            }

            .sk-chase {
                width: 40px;
                height: 40px;
                -webkit-animation: sk-chase 2.5s infinite linear both;
                animation: sk-chase 2.5s infinite linear both;
                margin: auto;
                z-index: 9999;
                position: absolute !important;
                top: 35% !important;
                // transform: translate(-35%, -35%) !important;
                left: 50% !important;
            }
        }
    }
}

.modal.bottom {
    .stage-header {
        padding: 0px !important;

        .close {
            padding: 0;
            margin: 0px;
            width: 36px;
            height: 41px;
        }
    }
}

.modal.bottom.fade {
    .modal-dialog {
        bottom: -100%;
        -webkit-transition:
            opacity 0.3s linear,
            bottom 0.3s ease-out;
        -moz-transition:
            opacity 0.3s linear,
            bottom 0.3s ease-out;
        -o-transition:
            opacity 0.3s linear,
            bottom 0.3s ease-out;
        transition:
            opacity 0.3s linear,
            bottom 0.3s ease-out;
    }
}

.modal.bottom.fade.show {
    .modal-dialog {
        bottom: 0;
    }
}

/* Right Modal */

.modal.right {
    .modal-dialog {
        position: fixed;
        margin: auto;
        height: 100%;
        -webkit-transform: translate3d(0%, 0, 0);
        -ms-transform: translate3d(0%, 0, 0);
        -o-transform: translate3d(0%, 0, 0);
        transform: translate3d(0%, 0, 0);
        background: #ffffff 0% 0% no-repeat padding-box;
        box-shadow: -6px -11px 6px #00000029;

        .modal-content {
            height: 100vh;
            border-radius: 0;
            border: none;

            .modal-header {
                padding: 5px 20px !important;
                border-bottom: none;
            }
        }
    }
}

.modal.right.fade {
    .modal-dialog {
        right: -80%;
        -webkit-transition:
            opacity 0.3s linear,
            right 0.3s ease-out;
        -moz-transition:
            opacity 0.3s linear,
            right 0.3s ease-out;
        -o-transition:
            opacity 0.3s linear,
            right 0.3s ease-out;
        transition:
            opacity 0.3s linear,
            right 0.3s ease-out;
    }
}

.modal.right.fade.show {
    .modal-dialog {
        right: 0;
        width: calc(100% - 20%) !important;
        max-width: calc(100% - 20%) !important;
    }
}

/* Top Modal */

.modal.top {
    .modal-dialog {
        position: fixed;
        margin: auto;
        right: 0px;
        left: 0px;
        z-index: 999;
        /*  width: 100%;
    max-width: 100%; */
        height: auto;
    }

    .modal-content {
        border-radius: 0;
        border: none;
        position: relative;

        .modal-header {
            padding: 5px 20px;

            .modal-title {
                color: #7044cd;
                font: normal normal normal 20px/1.5 Helvetica Neue;
                letter-spacing: 0px;
            }
        }
    }
}

.modal.top.fade {
    .modal-dialog {
        top: -100%;
        -webkit-transition:
            opacity 0.3s linear,
            top 0.3s ease-out;
        -moz-transition:
            opacity 0.3s linear,
            top 0.3s ease-out;
        -o-transition:
            opacity 0.3s linear,
            top 0.3s ease-out;
        transition:
            opacity 0.3s linear,
            top 0.3s ease-out;
    }
}

.modal.top.fade.show {
    .modal-dialog {
        top: 0;
    }
}

/* Pagination CSS */

.paginationFooter {
    background: #ffffff 0% 0% no-repeat padding-box;
    box-shadow: 0px -5px 6px #00000029;
    position: relative;
    display: inline-block;
    width: 100%;

    .paginationTotalCount {
        background: #ecf5f8;
        border-radius: 15px;
        color: #373737;
        font: normal normal normal 14px/1 Helvetica Neue;
        padding: 0.3rem 1.3rem;
        display: inline-block;
        // margin: 5px 0px;
    }

    .customPagination {
        /* padding: 0.3rem 1.3rem; */
        display: flex;
        justify-content: flex-end;

        .pagination {
            font-family: Helvetica, FontAwesome;
            margin: 5px 0px;

            .page-item {
                display: none;
            }

            .page-item:first-child,
            .page-item:nth-child(2),
            .page-item:last-child {
                display: block;
            }

            .page-item {
                text-align: center;
                border-radius: 0px;

                a {
                    background: #7044cd;
                    color: #fff;
                    border: none;
                    padding: 0.3rem 1.3rem;
                }

                a:focus {
                    box-shadow: none;
                }
            }

            .page-item:first-child {
                a {
                    border-bottom-left-radius: 15px;
                    border-top-left-radius: 15px;
                }
            }

            .page-item:last-child {
                a {
                    border-bottom-right-radius: 15px;
                    border-top-right-radius: 15px;
                }
            }

            .page-item:nth-child(2) {
                a {
                    background: #ecf5f8;
                    color: #3a3b3b;
                    padding: 0.3rem 0.5rem;
                }
            }

            .page-item.disabled {
                a {
                    background: #def0f6;
                    cursor: not-allowed;
                }
            }
        }
    }
}

/* Table CSS */

.tableFixHead {
    overflow-y: auto;
    max-height: calc(100vh - 30vh);
    min-height: calc(100vh - 30vh);
    height: calc(100vh - 30vh);
    border-bottom: 2px solid #b395f1;

    /* width */
    &::-webkit-scrollbar {
        width: 5px;
        height: 5px;
        cursor: pointer;
    }

    /* Track */
    &::-webkit-scrollbar-track {
        box-shadow: inset 0 0 5px #d2c1f3;
        border-radius: 10px;
    }

    /* Handle */
    &::-webkit-scrollbar-thumb {
        background: #7044cd;
        border-radius: 100px;
    }

    table {
        border-collapse: collapse;
        width: 99.9% !important;
        margin: 0px !important;
        z-index: 1;

        th,
        td {
            padding: 8px 16px;
        }

        thead {
            th {
                background: #f2f2f2;
                color: #707070;
                font: normal normal bold 13px/1.5 Helvetica Neue;
                position: sticky;
                top: 0;
                z-index: 1;
                box-shadow:
                    inset 0px 1px #d2c1f3,
                    0 1px #b395f1 !important;
                border-top: none;
                border-bottom: 2px solid #d2c1f3;
                border-collapse: collapse;
                white-space: nowrap;

                .sortIcon {
                    position: absolute;
                    right: 2px;
                    top: 13px;
                    color: #7044cd;
                    /* font-size: 1.3rem; */
                    z-index: 999;
                }
            }
        }

        tbody th {
            position: sticky;
            left: 0;
        }

        tbody td {
            background: #fff;
            color: #2a2a2a;
            font: normal normal 400 13px/1.5 Helvetica Neue;
            border: 1px solid #f9f6ff;
            white-space: nowrap;

            &.completed {
                color: #009845;
                // color: #a9e2af;
                font-weight: 500;
            }

            &.pending {
                color: #ffa21d;
                // color: #ffcf00;
                // color: #fdd285;
                font-weight: 500;
            }

            &.not-completed {
                color: #f20000;
                // color: #ffcf00;
                // color: #fdd285;
                font-weight: 500;
            }

            &.rejected {
                color: #ff5d5d;
                // color: #ffcf00;
                // color: #fdd285;
                font-weight: 500;
            }

            &.actionTd {
                background: #ecf4f8;
                text-align: center;
                margin: auto;

                // border-top-right-radius:10px;
                .editIcon {
                    color: #7044cd;
                    // color: #00aeef;
                    cursor: pointer;
                }

                .deleteIcon {
                    color: #f95371;
                    cursor: pointer;
                }

                .viewIcon {
                    color: #000;
                    cursor: pointer;
                }
            }

            .actionBtn,
            .actionBtn:hover,
            .actionBtn:focus {
                background: #7044cd;
                // background: #00aeef;
                color: #fff;
                text-transform: capitalize;
                // text-transform: uppercase;
                font: normal normal 400 10pt/1.5 Helvetica Neue;
                box-shadow: none;
                outline: none;
                border: none;
                border-radius: 4px;
                width: 100%;
                // box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            }

            &.no-record-found {
                background: transparent;
                // height: calc(100vh - 37vh);
                vertical-align: middle;
                border: none;
                position: fixed;
                left: 50%;
                top: 50%;
            }

            &.loading-div {
                background: transparent;
                height: calc(100vh - 37vh);
                vertical-align: middle;
                border: none;
                position: relative;
            }
        }

        .fixedColumn {
            position: sticky;
            top: 0px;
            /*             z-index: 999; */
            border-left: none;
            border-right: none;
        }

        .fixedColumn:last-child {
            right: 0px;
            box-shadow: -5px 0 5px -5px #333;
            // border-left: 1px solid #fff;
            // border-collapse: collapse;
        }

        .fixedColumn:first-child {
            left: 0px;
            box-shadow: 5px 0 5px -5px #333;
            // border-right: 1px solid #fff;
            // border-collapse: collapse;
        }

        .checkbox-block.form-check {
            position: relative;
            border: none;

            .form-check-label {
                padding: 0px;
                line-height: normal;
                width: auto;
                position: relative;
            }

            input[type="checkbox"]:checked+label::after {
                left: 0.23rem;
                top: 0.4rem;
            }

            input[type="checkbox"]+label::before {
                margin: 0px;
            }
        }

        .custom-selectField,
        .custom-selectField:hover,
        .custom-selectField:focus {
            box-shadow: none;
            border-radius: 0px;
            border: none;
            outline: none;
            appearance: none;
            background: url("../../assets/images/login/chevron-down.svg") transparent no-repeat calc(100% - 0px);
            font: normal normal bold 13px/24px Helvetica Neue;
            letter-spacing: 0px;
            color: #464646;
            background-size: contain;
            width: 165px;
        }

        /*For IE*/
        .custom-selectField::-ms-expand {
            display: none;
        }
    }
}

