        display: inline-flex;
        flex-direction: row;
        overflow-y: auto;
        justify-content: center;
        -ms-overflow-style: none;
        /* Internet Explorer 10+ */
        scrollbar-width: none;
    }

    .swiper-container::-webkit-scrollbar-track {
