.checkbox-block.form-check {
    border-bottom: 1px solid lightgray;
    position: relative;
    padding: 0.8rem 0px;

    .form-check-label {
        text-align: left;
        position: relative;
        font: normal normal 300 14px/1.5 Helvetica Neue;
        letter-spacing: 0px;
        color: #515774;
        cursor: pointer;
        padding: 5px 0px 5px 10px;
        vertical-align: middle;
        width: calc(100% - 20px);
    }

    input {
        cursor: pointer;
        margin: 0px;
        vertical-align: middle;
    }

    input[type="checkbox"] {
        position: absolute;
        /* right: -999em; */
        display: none;
    }

    input[type="checkbox"]:checked+label::after {
        border: 2px solid #7044cd;
        content: "";
        position: absolute;
        width: 1.9ex;
        height: 1ex;
        background: rgba(0, 0, 0, 0);
        top: 1.5ex;
        left: 1.9ex;
        border-top: none;
        border-right: none;
        transform: rotate(-45deg);
        z-index: 999;
    }

    input[type="checkbox"]+label::before {
        background: #ffffff 0% 0% no-repeat padding-box;
        border: 2px solid #c4a8ff;
        border-radius: 5px;
        content: "";
        display: inline-block;
        vertical-align: middle;
        height: 3ex;
        width: 3ex;
        margin-right: 0.5em;
    }

    input[type="checkbox"]:checked+label::before {
        /* background: #663cbf 0% 0% no-repeat padding-box; */
        border: 2px solid #7044cd;
    }
}

