                letter-spacing: 0px;
                color: #7044cd;
                text-transform: capitalize;
                padding: 0.5rem 0px;
            }

            .optText-div {
                position: relative;
                padding: 0.4rem 0px 0.4rem 1.25rem;
                border-bottom: 1px solid lightgray;

                label {
                    padding: 0.5rem 1rem 0.5rem 1rem;
                    width: 100%;
                    display: block;
                    cursor: pointer;
                    position: relative;
                    z-index: 2;
                    transition: color 200ms ease-in;
                    overflow: hidden;
                    margin: 0px;
                    font: normal normal normal 14px/1.5 Helvetica Neue;
                    letter-spacing: 0px;
                    color: #515774;
                    margin: 0px;
                    padding-left: 30px;
                    border-radius: 0px;
                    text-align: left;

                    &:before {
                        width: 10px;
                        height: 10px;
                        border-radius: 50%;
                        content: "";
                        background-color: transparent;
                        position: absolute;
                        left: 50%;
                        top: 50%;
                        transform: translate(-50%, -50%) scale3d(1, 1, 1);
                        transition: all 300ms cubic-bezier(0.4, 0, 0.2, 1);
                        opacity: 0;
                        z-index: -1;
                    }

                    &:after {
                        width: 20px;
                        height: 20px;
                        content: "";
                        border: 2px solid #d1d7dc;
                        background-color: #fff;
                        background-image: url(data:image/svg+xml;base64,);
                        background-repeat: no-repeat;
                        background-size: 10px;
                        background-position: center;
                        border-radius: 50%;
                        z-index: 2;
                        position: absolute;
                        left: 5px;
                        top: 50%;
                        transform: translateY(-50%);
                        cursor: pointer;
                        transition: all 200ms ease-in;
                    }
                }

                input:checked~label {
                    color: #7044cd;

                    &:before {
                        transform: translate(-50%, -50%) scale3d(56, 56, 1);
                        opacity: 1;
                    }

                    &:after {
                        background-color: #7044cd;
                        border-color: #1b8fc7;
                    }
                }

                input {
                    width: 20px;
                    height: 20px;
                    order: 1;
                    z-index: 2;
                    position: absolute;
                    left: 5px;
                    top: 50%;
                    transform: translateY(-50%);
                    cursor: pointer;
                    visibility: hidden;
                }
            }
        }
    }
}

