// =======================================
//     List of variables for layout
// =======================================

/*
    description Of variables for build for theme layouts
        1) menu-caption-color
            List of color for sidebar menu caption

        2) brand-background
            List of color for logo background

        3) header-dark-background
            List of color for Dark Header

        4) header-light-background
            List of color for light Header

        5) menu-dark-background
            List of color for Dark sidebar menu

        6) menu-light-background
            List of color for light sidebar menu

        7) menu-active-color
            List of color for Active item highliter

        8) menu-icon-color
            List of color for sidebar menu items icon colors
*/

$header-height: 95px;
// $Menu-width: 250px;
$Menu-width: 250px;
$Menu-collapsed-width: 70px;
// color list for build layouts
$primary-color: #4680ff;
$warning-color: #ffa21d;
$danger-color: #ff5252;
$success-color: #11c15b;
$purple-color: #7759de;
$info-color: #00acc1;
$secondary-color: #748892;
$default-color: #d6d6d6;
$light-color: #eceff1;
$dark-color: #263238;
$inverse-color: $dark-color;
$theme-border: #e2e5e8;
$custom-menu-background: #7044cd;
// $custom-menu-background:#00AEEF;
// header
$header-dark-background: #101b33;
$header-dark-text-color: #fff;
$header-light-background: #fff;
$header-light-text-color: #101b33;
// Menu
$menu-dark-background: #101b33;
// $menu-dark-text-color: #b5bdca;
$menu-dark-text-color: #97a7c1;
$menu-white-text-color: #fff;
$menu-light-background: #fff;
$menu-light-text-color: #535763;
$menu-active-color: $primary-color;
$dark-layout: darken(#101b33, 8%);
$dark-layout-font: #adb7be;
// Menu icon
$menu-icon-color: #4680ff, #ff5252, #01a9ac, #7759de, #00acc1, $warning-color;
// Header background
$color-header-name: blue, red, purple, info, dark;
$color-header-color: #4680ff, #ff5252, #7759de, #00acc1, #101b33;
// Menu background
$color-menu-name: blue, red, purple, info, dark;
$color-menu-color: #4680ff, #ff5252, #7759de, #00acc1, #101b33;
// Menu background gradient
$color-menu-gradient-name: blue, red, purple, info, dark;
$color-menu-gradient-color: linear-gradient(to bottom, #7759de 0%, #4680ff 100%),
    linear-gradient(to bottom, rgb(164, 69, 178) 0%, rgb(212, 24, 114) 52%, rgb(255, 0, 102) 100%),
    linear-gradient(to top, rgb(178, 117, 240) 0%, rgb(117, 117, 240) 100%),
    linear-gradient(to bottom, rgb(1, 169, 172) 0%, rgb(1, 219, 223) 100%),
    linear-gradient(to bottom, rgb(36, 46, 62) 0%, rgb(58, 76, 103) 100%);
// Active background color
$color-active-name: blue, red, purple, info, dark;
$color-active-color: #4680ff, #ff5252, #7759de, #00acc1, $dark-color;
// Menu title color
$color-title-name: blue, red, purple, info, dark;
$color-title-color: #4680ff, #ff5252, #7759de, #00acc1, $dark-color;
$theme-font-family: "Helvetica Neue", sans-serif;
$theme-font-size: 0.875rem;
$theme-background: #f4f5fa;
$theme-font-color: #535763;
// $theme-heading-color: #111;
$theme-heading-color: #060606;
$color-name: c-blue, c-red, c-green, c-yellow, c-purple;
$color-color: $primary-color, $danger-color, $success-color, $warning-color, $purple-color;
$color-bt-name: primary, danger, success, warning, info, purple;
$color-bt-color: $primary-color, $danger-color, $success-color, $warning-color, $info-color, $purple-color;
// form-control variables
$form-bg: #f0f3f6;
// social color variables
$social-name: facebook, twitter, dribbble, pinterest, youtube, googleplus, instagram, viber, behance, dropbox, linkedin;
$social-color: #3c5a99, #42c0fb, #ec4a89, #bf2131, #e0291d, #c73e2e, #aa7c62, #7b519d, #0057ff, #3380ff, #0077b5;
// =====================================================================
// ==================   Bootstrap Extra Variable  ======================
// =====================================================================
// Color contrast
$yiq-contrasted-threshold: 200;
$yiq-dark-color: #37474f;
$yiq-light-color: #fff;
$theme-color-interval: 8%;
$alert-bg-level: -10;
$alert-border-level: -9;
$alert-color-level: 6;
// card block css
$card-shadow: 0 1px 3px 0 rgba(54, 80, 138, 0.3);
$card-header-border: 0px solid #e2e5e8;
