/* new logo start */

.b-brand {
    // display: flex;
    align-items: center;
    display: inline;
    width: 95%;
    text-align: center;
    .b-bg {
        background: $primary-color;
        border-radius: 3px;
        width: 30px;
        height: 30px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        font-size: 18px;
        color: #fff;
    }
    .b-title {
        margin-left: 10px;
        font-weight: 400;
        color: #fff;
        font-size: 16px;
    }
}

.navbar-collapsed {
    .b-brand .b-title {
        transform: rotateY(-90deg);
        transform-origin: 0 0;
        opacity: 0;
        transition: all 0.3s ease-in-out;
    }
    .mobile-menu {
        transform: rotateY(-90deg);
        transform-origin: 0 0;
        opacity: 0;
        transition: all 0.3s ease-in-out;
    }
    &:hover {
        .b-brand .b-title {
            transform: rotateY(0deg);
            opacity: 1;
        }
        .mobile-menu {
            transition-delay: 0.3s;
            transform: rotateY(0deg);
            opacity: 1;
        }
    }
}

/* new logo End  */

/* Pre-loader css start */

.loader-bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 3px;
    z-index: 1035;
    box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.2);
    .loader-track {
        position: relative;
        height: 3px;
        display: block;
        width: 100%;
        overflow: hidden;
        .loader-fill {
            &:after,
            &:before {
                content: "";
                background: #fff;
                position: absolute;
                top: 0;
                left: 0;
                bottom: 0;
                will-change: left, right;
            }
            &:before {
                animation: mbar 2.1s cubic-bezier(0.65, 0.815, 0.735, 0.395) infinite;
            }
            &:after {
                animation: m_s 2.1s cubic-bezier(0.165, 0.84, 0.44, 1) infinite;
                animation-delay: 1.15s;
            }
        }
    }
}

@keyframes mbar {
    0% {
        left: -35%;
        right: 100%;
    }
    60% {
        left: 100%;
        right: -90%;
    }
    100% {
        left: 100%;
        right: -35%;
    }
}

@keyframes m_s {
    0% {
        left: -200%;
        right: 100%;
    }
    60% {
        left: 107%;
        right: -8%;
    }
    100% {
        left: 107%;
        right: -8%;
    }
}

/* Pre-loader css end */

/* header css start */

.pcoded-header {
    z-index: 1029;
    position: relative;
    display: flex;
    min-height: $header-height;
    padding: 0;
    top: 0;
    color: transparentize($header-light-text-color, 0.2);
    /* width: 100%; */
    width: auto;
    transition: all 0.3s ease-in-out;
    /* background: $header-light-background;
    box-shadow: 0 1px 3px 0 rgba(54, 80, 138, 0.3); */
    background: transparent;
    box-shadow: none;
    .m-header {
        width: $Menu-width;
        position: relative;
        height: $header-height;
        display: inline-flex;
        align-items: center;
        padding: 0 25px;
        background: $custom-menu-background;
        // background: #00AEEF;
        .logo-dark,
        .logo-thumb {
            display: none;
        }
    }
    .input-group {
        background: transparent;
        .input-group-text {
            margin-right: 0;
        }
    }
    .input-group .input-group-text,
    a,
    dropdown-toggle {
        color: transparentize($header-light-text-color, 0.2);
        &:hover {
            color: $header-light-text-color;
        }
    }
    #mobile-header {
        display: none;
    }
    .navbar-nav {
        padding-left: 0;
        list-style: none;
        margin-bottom: 0;
        display: inline-block;
        > li {
            line-height: $header-height;
            display: inline-block;
            padding: 0 12px;
            .nav-link {
                padding: 0;
            }
            &:first-child {
                padding-left: 25px;
            }
            &:last-child {
                padding-right: 25px;
            }
        }
    }
    .mr-auto {
        .dropdown-menu {
            margin-left: -20px;
        }
    }
    .ml-auto {
        float: right;
        .dropdown-menu {
            margin-right: -20px;
        }
    }
    .main-search {
        .input-group {
            border-radius: 3px;
            padding: 0;
            .form-control,
            .input-group-text {
                background: transparent;
                border: none;
                border-radius: 0;
                padding: 0;
                height: auto;
            }
            .search-close {
                display: none;
            }
            .search-btn {
                padding: 0;
                background: transparent;
                border-color: transparent;
                box-shadow: none;
                margin-bottom: 0;
                margin-right: 0;
            }
            .form-control {
                transition: all 0.3s ease-in-out;
                // width: 0;
                font-size: 14px;
            }
            .form-control,
            .search-btn {
                &:active,
                &:focus,
                &:hover {
                    outline: none;
                    box-shadow: none;
                }
            }
        }
        &.open {
            .input-group {
                background: $body-bg;
                border: 0 solid $body-bg;
                padding: 0 0 0 20px;
                .search-close {
                    display: flex;
                }
                .search-btn {
                    padding: 6px 9px;
                    margin-left: 5px;
                    color: #fff;
                    background: $primary-color;
                    border-color: $primary-color;
                    .input-group-text {
                        color: #fff;
                    }
                }
                .form-control {
                    transition: all 0.3s ease-in-out;
                    // width: 0;
                }
                .form-control,
                .search-btn {
                    &:active,
                    &:focus,
                    &:hover {
                        outline: none;
                        box-shadow: none;
                    }
                }
            }
        }
    }
    .dropdown {
        .dropdown-toggle {
            line-height: $header-height;
            display: inline-block;
            padding-right: 5px;
            &:after {
                display: none;
            }
            &:before {
                content: "";
                width: 30px;
                height: 30px;
                position: absolute;
                background: rgba(255, 255, 255, 0.25);
                top: calc(#{$header-height/2} - 17px);
                left: -8px;
                border: none;
                border-radius: 50%;
            }
        }
        .dropdown-menu {
            position: absolute;
            min-width: 250px;
            li {
                line-height: 1.2;
                a {
                    padding: 10px;
                    font-size: 14px;
                }
                &.active,
                &:active,
                &:focus,
                &:hover {
                    a {
                        color: $theme-font-color;
                    }
                }
            }
        }
        .notification {
            width: 350px;
            padding: 0;
            line-height: 1.4;
            overflow: hidden;
            a {
                color: $theme-font-color;
                &:hover {
                    color: $theme-heading-color;
                }
            }
            .noti-head {
                background: $menu-dark-background;
                padding: 15px 20px;
                h6 {
                    color: #fff;
                }
                a {
                    color: #fff;
                    text-decoration: underline;
                    font-size: 13px;
                }
            }
            .noti-body {
                padding: 0;
                position: relative;
                height: 300px;
                img {
                    width: 40px;
                    margin-right: 20px;
                }
                li {
                    padding: 15px 20px;
                    transition: all 0.3s ease-in-out;
                    &.n-title {
                        padding-bottom: 0;
                        p {
                            margin-bottom: 5px;
                        }
                    }
                    &.notification:hover {
                        background: transparentize($primary-color, 0.9);
                    }
                    p {
                        margin-bottom: 5px;
                        font-size: 13px;
                        strong {
                            color: #222;
                        }
                    }
                    .n-time {
                        font-size: 80%;
                        float: right;
                    }
                }
            }
            .noti-footer {
                border-top: 1px solid #f1f1f1;
                padding: 15px 20px;
                text-align: center;
                background: $theme-background;
                a {
                    text-decoration: underline;
                    font-size: 13px;
                }
            }
            ul {
                padding-left: 0;
                margin-bottom: 0;
                list-style: none;
                li {
                    padding: 20px 15px;
                }
            }
        }
        .profile-notification {
            padding: 0;
            line-height: 1.4;
            overflow: hidden;
            .pro-head {
                color: #fff;
                padding: 15px;
                position: relative;
                background: $custom-menu-background;
                /* background: $menu-dark-background; */
                img {
                    width: 40px;
                    margin-right: 10px;
                }
                .dud-logout {
                    color: #fff;
                    padding-right: 0;
                    position: absolute;
                    right: 0;
                    top: 0;
                    height: 100%;
                    width: 40px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
            }
            .pro-body {
                padding: 0px 0;
                margin-bottom: 0;
                list-style: none;
                li {
                    margin: 0 0px;
                    /* border-radius: 2px; */
                    a {
                        color: $theme-font-color;
                        font-size: 14px;
                        padding: 10px;
                        i {
                            margin-right: 10px;
                        }
                    }
                    &.active,
                    &:active,
                    &:focus,
                    &:hover {
                        background: transparentize($custom-menu-background, 0.4);
                        box-shadow: 0 15px 8px -9px rgba(0, 0, 0, 0.25);
                        > a {
                            color: #fff;
                            background: transparent;
                        }
                    }
                }
            }
        }
    }
}

/* header css end */

