        .pcoded-inner-navbar {
            > li {
                > a {
                    padding: 7px 20px;
                    width: calc(100% - 2em);
                    // margin: 0;
                    > .pcoded-micon + .pcoded-mtext {
                        position: absolute;
                        top: 11px;
                    }
                    > .pcoded-mtext {
                        transform: rotateY(-90deg);
                        transform-origin: 0 0;
                        opacity: 0;
                        transition: all 0.3s ease-in-out;
                    }
                }
                &.pcoded-hasmenu {
                    > a {
                        &:after {
                            right: 12px;
                        }
                    }
                    .pcoded-submenu {
                        display: none;
                    }
                }
            }
            > li {
                &.pcoded-trigger {
                    .pcoded-submenu {
                        &:after {
                            // content: "";
                            // position: absolute;
                            // top: 50px;
                            // left: calc(calc(#{$Menu-collapsed-width} / 2) + 2px);
                            // width: 1px;
                            // height: calc(100% - 50px);
                            // background: transparentize($menu-dark-text-color,0.8);
                        }
                        li {
                            a {
                                color: transparent;
                                white-space: nowrap;
                            }
                            + li {
                                a {
                                    border-top: none;
                                }
                            }
                        }
                        .pcoded-submenu {
                            li {
                                a:before {
                                    opacity: 0;
                                }
                            }
                        }
                    }
                }
            }
        }
        ~ .pcoded-header {
            // width: calc(100% - #{$Menu-collapsed-width});
        }
        // ~ .pcoded-header,
        ~ .pcoded-main-container {
            margin-left: $Menu-collapsed-width;
        }
        .pcoded-badge {
            transform: rotateX(-90deg);
            transform-origin: 50% 50%;
            opacity: 0;
            display: none;
            transition: all 0.15s ease-in-out;
        }
        &:hover {
            width: $Menu-width !important;
            .header-logo {
                width: $Menu-width;
                img {
                    transform: rotateY(0deg);
                    opacity: 1;
                }
                .logo-thumb {
                    transform: rotateY(-90deg);
                    opacity: 0;
                }
                .mobile-menu {
                    display: flex;
                    right: 10px;
                }
            }
            .pcoded-menu-caption {
                > label {
                    transform: rotateY(0deg);
                    opacity: 1;
                }
                &:after {
                    background: transparent;
                }
            }
            .mobile-menu {
                transform: rotateY(0deg);
                opacity: 1;
            }
            .pcoded-inner-navbar {
                > li {
                    > a {
                        // margin: 5px 10px;
                        > .pcoded-mtext {
                            transform: rotateY(0deg);
                            opacity: 1;
                            &:after {
                                opacity: 1;
                                visibility: visible;
                            }
                        }
                    }
                    &.pcoded-hasmenu {
                        > a {
                            &:after {
                                transform: rotateX(0deg);
                            }
                        }
                        &.pcoded-trigger {
                            > a {
                                &:after {
                                    transform: rotateX(0deg) rotate(90deg);
                                }
                            }
                        }
                    }
                }
                > li {
                    &.pcoded-trigger {
                        .pcoded-submenu {
                            &:after {
                                // display: none;
                            }
                            li {
                                a {
                                    color: inherit;
                                }
                            }
                            .pcoded-submenu {
                                li {
                                    a:before {
                                        opacity: 1;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    .pcoded-menu-caption {
        color: #8090a5;
    }
    .pcoded-inner-navbar {
        li {
            &.active,
            &.active:hover,
            &.active:focus {
                > a {
                    color: #e5efff;
                    font-weight: 500;
                    // background: #0033b3;
                    background: #9665ff;
                    // background: #002e6d;
