        .navbar-nav.mr-auto {
            li {
                // display: none;
                &.nav-item:not(.dropdown) {
                    display: block;
                    padding-right: 0;
                    .search-close {
                        display: none;
                    }
                }
            }
        }
        .dropdown {
            &.show {
                position: static;
                a:after {
                    display: none;
                }
                &:before {
                    display: none;
                }
            }
            .notification {
                width: 100%;
            }
            .dropdown-menu {
                width: 95%;
                margin-left: auto;
                margin-right: auto;
                left: 0;
                right: 0;
