                    transition: none;
                }
            }
            .pcoded-menu-caption {
                > label {
                    transition: none;
                }
            }
            .pcoded-inner-navbar {
                > li {
                    > a {
                        > .pcoded-mtext {
                            transition: none;
                        }
                    }
                    &.pcoded-hasmenu {
                        > a {
                            &:after {
                                transition: none;
                                transition-delay: 0;
                            }
                        }
                    }
                }
            }
            .pcoded-badge {
                transition: none;
            }
            &:hover {
                .pcoded-badge {
                    transition-delay: 0;
                }
            }
        }
    }
}

/* responsive horizontal menu */

@media only screen and (max-width: 991px) {
    .pcoded-navbar {
        &.theme-horizontal {
            margin-left: 0;
