/* * Horizontal */

.theme-horizontal {
    .sidenav-horizontal-wrapper {
        flex: 1 1 auto;
        width: 0;
        .sidenav:not(.sidenav-no-animation) & .sidenav-inner {
            transition: margin 0.2s;
        }
    }
    .sidenav-horizontal-next,
    .sidenav-horizontal-prev {
        position: relative;
        display: block;
        flex: 0 0 auto;
        width: 2.25rem;
        background-color: $menu-dark-background;
        z-index: 9;
        &::after {
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            display: block;
            width: 0.8rem;
            height: 0.8rem;
            border: 2px solid;
            border-top: 0;
        }
        &.disabled {
            cursor: default !important;
            opacity: 0;
        }
    }
    .sidenav-horizontal-prev::after {
        border-right: 0;
        transform: translate(-50%, -50%) rotate(45deg);
    }
    .sidenav-horizontal-next::after {
        border-left: 0;
        transform: translate(-50%, -50%) rotate(-45deg);
    }
    .sidenav-horizontal {
        &:after,
        &:before {
            content: "";
            background: $menu-dark-background;
            width: 100%;
            position: absolute;
            top: 0;
            height: 100%;
            z-index: 5;
            @media only screen and (max-width: 991px) {
                display: none;
            }
        }
        &:before {
            left: 100%;
        }
        &:after {
            right: 100%;
        }
    }
    &.menu-light {
        .sidenav-horizontal {
            &:after,
            &:before {
                background: $menu-light-background;
            }
        }
    }
}

@keyframes sidenavDropdownShow {
    0% {
        opacity: 0;
        transform: translateY(-0.5rem);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.pcoded-navbar {
    .card {
        background: rgba(255, 255, 255, 0.1);
        margin: 20px;
        box-shadow: 0 0 0 1px transparentize($theme-border, 0.85);
        color: rgba(255, 255, 255, 0.6);
        h6 {
            color: rgba(255, 255, 255, 0.6);
        }
        .close {
            position: absolute;
            color: $menu-dark-text-color;
            position: absolute;
            top: 10px;
            right: 12px;
            opacity: 0.8;
            text-shadow: none;
        }
    }
    &.menu-light {
        .card {
            background: rgba(0, 0, 0, 0.1);
            box-shadow: 0 0 0 1px $theme-background;
            color: rgba(0, 0, 0, 0.6);
            h6 {
                color: $menu-light-text-color;
            }
            .close {
                color: $menu-light-text-color;
            }
        }
    }
    &.navbar-collapsed {
        .card {
            display: none;
        }
    }
}

// ==========================    Menu Styler Start     ======================
$style-block-width: 400px;
.menu-styler {
    z-index: 1029;
    position: relative;
    color: $theme-font-color;
    h5,
    h6 {
        color: $theme-heading-color;
    }
    hr {
        border-top: 1px solid rgba(0, 0, 0, 0.1);
    }
    .style-toggler {
        > a {
            position: fixed;
            right: 0;
            top: 200px;
            z-index: 1031;
            transition: 0.3s ease-in-out;
            &:before {
                content: "";
                width: 45px;
                height: 45px;
                position: absolute;
                right: 0;
                top: 0;
                z-index: 1001;
                background: $danger-color;
                transition: 0.3s ease-in-out;
                border-radius: 3px 0 0 3px;
                box-shadow:
                    -4px 0 8px transparentize($danger-color, 0.84),
                    -6px 0 8px rgba(0, 0, 0, 0.1);
            }
            &:after {
                content: "\e849";
                top: 12px;
                font-size: 21px;
                position: absolute;
                right: 11px;
                font-family: "feather";
                z-index: 1029;
                line-height: 1;
                color: #fff;
                transition: 0.3s ease-in-out;
                @media all and (-ms-high-contrast: none) {
                    line-height: 0.5;
                    top: 18px;
                }
            }
        }
    }
    &.open,
    &.prebuild-open {
        .style-toggler {
            > a {
                right: $style-block-width;
                animation: sh-shake 0s ease infinite;
                &:after {
                    content: "\e847";
                    animation: anim-rotate 0s linear infinite;
                }
            }
        }
        .prebuild-toggler > .prebuild-group {
            right: calc(#{$style-block-width} - 100px);
        }
    }
    &.open {
        .style-block {
            right: 0;
            box-shadow: -6px 0 12px rgba(0, 0, 0, 0.175);
        }
    }
    &.prebuild-open {
        .style-block-prebuild {
            right: 0;
            box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
        }
    }
    .style-block,
    .style-block-prebuild {
        position: fixed;
        top: 140px;
        right: -#{$style-block-width};
        width: $style-block-width;
        border-radius: 4px 0 0 4px;
        background: #fff;
        z-index: 1030;
        transition: 0.3s ease-in-out;
        padding: 25px 20px 20px;
    }
    .mst-scroll {
        height: calc(100vh - 175px);
        background-color: transparentize($primary-color, 0.97);
        padding: 0 15px;
    }
    label {
        margin-bottom: 3px;
    }
    .switch input[type="checkbox"] + .cr {
        top: 8px;
    }
    .radio {
        input[type="radio"] {
            + .cr {
                margin-right: 10px;
                &:after,
                &:before {
                    margin-right: 3px;
                }
            }
        }
    }
    /*h6 {
        position: relative;
        margin: 20px 0 10px;
    }*/
    .nav-pills {
        padding: 0;
        box-shadow: none;
        li {
            width: calc(100% / 3);
        }
    }
    .m-style-scroller {
        padding: 15px 20px;
        box-shadow: none;
        background-color: $body-bg;
        border-radius: 4px;
        box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.24);
    }
    @media only screen and (max-width: 992px) {
        display: none;
    }
}

@keyframes sh-shake {
    0%,
    100%,
    25% {
        transform: scale(1);
    }
    15%,
    3%,
    9% {
        transform: scale(1.1);
    }
    12%,
    20%,
    6% {
        transform: scale(0.9);
    }
}

.lay-customizer .theme-color,
.theme-color {
    display: block;
    position: relative;
    padding: 0;
    margin-bottom: 10px;
    > a {
        position: relative;
        width: 45px;
        height: 30px;
        border-radius: 3px;
        display: inline-block;
        background: $theme-background;
        overflow: hidden;
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.28);
        + a {
            margin-left: 8px;
        }
        span {
            width: 100%;
            position: absolute;
            &:after,
            &:before {
                content: "";
                height: 100%;
                position: absolute;
            }
            &:before {
                width: 32%;
                left: 0;
                background: $menu-dark-background;
            }
            &:after {
                width: 70%;
                right: 0;
                background: $theme-background;
            }
        }
        > span:nth-child(1) {
            height: 30%;
            top: 0;
            &:after {
                background: $header-light-background;
            }
        }
        > span:nth-child(2) {
            height: 75%;
            bottom: 0;
        }
        &.active {
            &:before {
                content: "\e83e";
                top: 1px;
                font-size: 20px;
                position: absolute;
                left: 12px;
                font-family: "feather";
                z-index: 1001;
                color: $primary-color;
                transition: 0.3s ease-in-out;
                text-shadow:
                    0 1px 3px transparentize($primary-color, 0.1),
                    0 3px 8px rgba(0, 0, 0, 0.1);
            }
            &:after {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: rgba(255, 255, 255, 0.7);
            }
        }
        &[data-value="menu-light"] {
            span {
                &:before {
                    background: $menu-light-background;
                }
            }
        }
        &[data-value="dark"] {
            span {
                &:after {
                    background: darken($dark-layout, 7%);
                }
            }
        }
        &[data-value="reset"] {
            background: $danger-color;
            color: #fff;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            top: -10px;
            width: 135px;
            box-shadow: none;
        }
    }
    &.small {
        > a {
            width: 25px;
            height: 25px;
            border-radius: 50%;
            &:before {
                font-size: 15px;
                left: 5px;
                top: 1px;
            }
            > span {
                display: none;
            }
        }
    }
    &.header-color {
        @each $value in $color-header-name {
            $i: index($color-header-name, $value);
            > a {
                &[data-value="header-#{'' + $value}"] {
                    > span:nth-child(1) {
                        &:after {
                            background: nth($color-header-color, $i);
                        }
                    }
                }
            }
            &.small > a {
                &[data-value="header-#{''+$value}"] {
                    background: nth($color-header-color, $i);
                }
            }
        }
    }
    &.navbar-color {
        @each $value in $color-menu-name {
            $i: index($color-menu-name, $value);
            > a {
                &[data-value="navbar-#{''+$value}"] {
                    > span:nth-child(2) {
                        &:before {
                            background: nth($color-menu-color, $i);
                        }
                    }
                }
            }
            // &.small > a {
            //     &[data-value="navbar-#{"" + $value}"] {
            //         background: nth($color-menu-color, $i);
            //     }
            // }
        }
    }
    &.brand-color {
        @each $value in $color-header-name {
            $i: index($color-header-name, $value);
            > a {
                &[data-value="brand-#{''+$value}"] {
                    > span:nth-child(1) {
                        &:before {
                            background: nth($color-header-color, $i);
                        }
                    }
                }
            }
            &.small > a {
                &[data-value="brand-#{''+$value}"] {
                    background: nth($color-header-color, $i);
                }
            }
        }
    }
}

.doc-img {
    > a {
        position: relative;
        width: 130px;
        height: 80px;
        display: inline-block;
        background: $theme-background;
        overflow: hidden;
        box-shadow: 0 0 0 1px #e2e5e8;
        span {
            width: 100%;
            position: absolute;
            &:after,
