                left: 0;
                background: $menu-dark-background;
            }
            &:after {
                width: 70%;
                right: 0;
                background: $theme-background;
            }
        }
        > span:nth-child(1) {
            height: 20%;
            top: 0;
            &:after {
                background: $header-light-background;
            }
        }
        > span:nth-child(2) {
            height: 80%;
            bottom: 0;
        }
        &.active {
            border-color: $primary-color;
            &:before {
                content: "\e83f";
                top: -4px;
                font-size: 20px;
                position: absolute;
                left: 10px;
                font-family: "feather";
                z-index: 1001;
                color: $primary-color;
                transition: 0.3s ease-in-out;
                text-shadow:
                    0 3px 8px transparentize($primary-color, 0.1),
                    0 3px 8px rgba(0, 0, 0, 0.1);
            }
            &:after {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: rgba(255, 255, 255, 0.65);
            }
        }
        &[data-value="menu-light"] {
            span {
                &:before {
                    background: $menu-light-background;
                }
            }
        }
        &[data-value="dark"] {
            span {
                &:after {
                    background: darken($dark-layout, 7%);
                }
            }
        }
    }
    &.header-color {
        @each $value in $color-header-name {
            $i: index($color-header-name, $value);
            > a {
                &[data-value="header-#{''+$value}"] {
                    > span:nth-child(1) {
                        &:after {
                            background: nth($color-header-color, $i);
                        }
                    }
                }
            }
        }
    }
    &.brand-color {
        @each $value in $color-header-name {
            $i: index($color-header-name, $value);
            > a {
                &[data-value="brand-#{''+$value}"] {
                    > span:nth-child(1) {
                        &:before {
                            background: nth($color-header-color, $i);
                        }
                    }
                }
            }
        }
    }
}

