/* ===================================================================================
 ==========================    Diffrent Layout Styles Start     ====================
 ===================================================================================
 ====== [ light menu style start ] ======== */

.pcoded-navbar.menu-light {
    background-color: $menu-light-background;
    color: $menu-light-text-color;
    .header-logo {
        background-color: $menu-dark-background;
        .logo-dark {
            display: none;
        }
        .logo-main {
            display: block;
        }
    }
    .sidenav-horizontal-next,
    .sidenav-horizontal-prev {
        background: #fff;
    }
    .mobile-menu {
        span {
            background-color: transparentize($header-dark-text-color, 0.2);
            &:after,
            &:before {
                background-color: transparentize($header-dark-text-color, 0.2);
            }
        }
        &.on {
            span {
                background-color: transparent;
            }
        }
    }
    .pcoded-inner-navbar {
        li.pcoded-menu-caption {
            border-top-color: $theme-border;
        }
        li.pcoded-hasmenu .pcoded-submenu {
            &:after {
                background: transparentize($menu-white-text-color, 0.5);
            }
            li {
                &:after {
                    background: transparentize($menu-white-text-color, 0.5);
                }
                &.active,
                &.pcoded-trigger,
                &:hover {
                    > a {
                        &:before {
                            background: $primary-color;
                        }
                    }
                }
            }
        }
        li.pcoded-hasmenu .pcoded-submenu li + li {
            > a {
                border-top-color: #ededed;
            }
        }
    }
    .pcoded-submenu {
        background: $menu-light-background;
    }
    a {
        color: $menu-light-text-color;
    }
    &.navbar-collapsed {
        .pcoded-menu-caption {
            &:after {
                background: rgba(0, 0, 0, 0.1);
            }
        }
        &:hover {
            .pcoded-menu-caption {
                &:after {
                    background: transparent;
                }
            }
        }
    }
    .pcoded-menu-caption {
        color: $menu-light-text-color;
    }
    .pcoded-inner-navbar {
        > li {
            &.active,
            &.pcoded-trigger {
                > a {
                    // background: $primary-color;
                    // color: #fff;
                }
            }
        }
        li {
            &.active,
            &.pcoded-trigger,
            &:hover {
                > a {
                    color: #000;
                    background: transparent;
                }
            }
            &.pcoded-hasmenu {
                // &.active,
                // &.pcoded-trigger {
                //     &:before {
                //         color: $menu-light-background;
                //     }
                // }
            }
        }
    }
    &.theme-horizontal {
        .pcoded-inner-navbar {
            > li {
                &.active,
                &.pcoded-trigger,
                &:hover {
                    a {
                        color: $menu-light-text-color;
                        background: transparent;
                    }
                }
                &.pcoded-hasmenu {
                    &.active,
                    &.pcoded-trigger {
                        &:before {
                            color: $menu-light-background;
                        }
                    }
                }
            }
        }
    }
}

/* ====== [ light menu style end ] ======== */

/* ====== [ Header color start ] ======== */

.pcoded-header {
    &[class*="header-"] {
        .mobile-menu {
            span {
                background-color: transparentize($header-dark-text-color, 0.2);
                &:after,
                &:before {
                    background-color: transparentize($header-dark-text-color, 0.2);
                }
            }
            &:hover {
                span {
                    background-color: #fff;
                    &:after,
                    &:before {
                        background-color: #fff;
                    }
                }
            }
        }
        @media only screen and (min-width: 992px) {
            .collapse:not(.show) {
                background: inherit;
            }
            .page-header {
                .breadcrumb-item + .breadcrumb-item::before,
                .page-header-title + .breadcrumb > .breadcrumb-item a,
                h5 {
                    color: #fff;
                }
            }
        }
    }
    @each $value in $color-header-name {
        $i: index($color-header-name, $value);
        &.header-#{"" + $value} {
            background: nth($color-header-color, $i);
            color: rgba(256, 256, 256, 0.8);
            box-shadow: 0 1px 3px 0 transparentize(nth($color-header-color, $i), 0.7);
            .profile-notification {
                li {
                    > a {
                        color: $theme-font-color;
                    }
                    &.active,
                    &:active,
                    &:focus,
                    &:hover {
                        background: transparentize(nth($color-header-color, $i), 0.9);
                        > a {
                            background: transparent;
                        }
                    }
                }
            }
            .dropdown-menu {
                color: $theme-font-color;
                a {
                    color: $theme-font-color;
                }
                > li {
                    > a {
                        color: $theme-font-color;
                    }
                    &.active,
                    &:active,
                    &:focus,
                    &:hover {
                        background: transparentize(nth($color-header-color, $i), 0.9);
                        color: $theme-font-color;
                        > a {
                            background: transparent;
                        }
                    }
                }
            }
            .input-group .input-group-text,
            a,
            dropdown-toggle {
                color: #fff;
                &:hover {
                    color: $menu-light-text-color;
                }
                @media only screen and (max-width: 991px) {
                    color: $menu-light-text-color;
                    &:hover {
                        color: darken($menu-light-text-color, 10%);
                    }
                }
            }
            @media only screen and (max-width: 991px) {
                .m-header > a {
                    color: #fff;
                }
            }
            .main-search {
                .search-close > .input-group-text {
                    color: nth($color-header-color, $i);
                }
                &.open {
                    .input-group {
                        background: rgba(255, 255, 255, 0.25);
                        border: 0 solid rgba(255, 255, 255, 0.25);
                        padding: 2px 2px 2px 20px;
                        @media only screen and (max-width: 991px) {
                            background: $body-bg;
                        }
                        @media only screen and (min-width: 992px) {
                            .form-control {
                                color: #fff;
                                &::-moz-placeholder {
                                    color: #fff;
                                    opacity: 1;
                                }
                                &:-ms-input-placeholder {
                                    color: #fff;
                                }
                                &::-webkit-input-placeholder {
                                    color: #fff;
                                }
                            }
                        }
                        .search-btn {
                            background: nth($color-header-color, $i);
                            border-color: nth($color-header-color, $i);
                            .input-group-text {
                                color: #fff;
                            }
                        }
                    }
                }
            }
            .dropdown {
                .notification {
                    .noti-head {
                        a {
                            color: #fff;
                        }
                    }
                    .noti-body {
                        li {
                            &.notification:hover {
                                background: transparentize(nth($color-header-color, $i), 0.9);
                            }
                        }
                    }
                }
                .profile-notification {
                    .pro-head {
                        color: #fff;
                        background: nth($color-header-color, $i);
                        .dud-logout {
                            color: #fff;
                        }
                    }
                }
            }
            .b-bg {
                background: #fff;
                color: nth($color-header-color, $i);
            }
        }
    }
}

/* ====== [ Header color end ] ======== */

/* ====== [ Brand color start ] ======== */

.pcoded-header {
    &[class*="brand-"]:not(.brand-default) {
        .mobile-menu {
            span,
            span:after,
            span:before {
                background: #fff;
            }
            &.on span {
                background: transparent;
            }
        }
        .b-bg {
            background: #fff;
            color: nth($color-header-color, 1);
        }
    }
    @each $value in $color-header-name {
        $i: index($color-header-name, $value);
        &.brand-#{"" + $value} .m-header,
        &[class*="header-"].brand-#{"" + $value} .m-header {
            background: nth($color-header-color, $i);
            @if ($value== "dark") {
                .b-bg {
                    background: nth($color-header-color, 1);
                    color: #fff;
                }
            } @else {
                .b-bg {
                    background: #fff;
                    color: nth($color-header-color, $i);
                }
            }
        }
    }
    &.brand-default .header-logo,
    &[class*="header-"].brand-default .header-logo {
        background: $menu-dark-background;
    }
}

/* ====== [ Brand color end ] ======== */

/* ====== [ menu fixed start ] ======== */

.pcoded-navbar.menupos-fixed {
    position: fixed;
    height: 100vh;
    min-height: 100vh;
    .scroll-div.navbar-content {
        height: calc(100% - #{$header-height});
        min-height: calc(100% - #{$header-height});
    }
}

/* ====== [ menu fixed end ] ======== */

/* ====== [ Header Fixed start ] ======== */

.pcoded-header.headerpos-fixed {
    position: fixed;
    top: 0;
    ~ .pcoded-main-container {
        /* padding-top: $header-height; */
        padding-top: 0;
        @media only screen and (max-width: 991px) {
            /* padding-top: $header-height; */
            padding-top: 0;
        }
    }
}

/* ====== [Header Fixed  end ] ======== */

/* ====== [Box start ] ======== */

body.box-layout {
    position: static;
    background-size: auto;
    background-color: #797979;
    //background: $theme-background;
    .pcoded-header {
        position: relative;
        ~ .pcoded-main-container {
            padding-top: 0;
            background: $theme-background;
        }
    }
    .pcoded-navbar {
        position: absolute;
        align-content: space-around;
        height: auto;
        &.theme-horizontal {
            ~ .pcoded-header {
                position: relative;
            }
        }
    }
    @media only screen and (max-width: 992px) {
        position: relative;
    }
}

@media only screen and (max-width: 992px) {
    .container {
        padding: 0;
    }
}

/* ====== [ Box end ] ======== */
